.btn-logout {
    width: 100%;
    padding: 12px;
    background-color: #4a00e0;
    color: #ffffff;
    font-weight: bold;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .btn-logout:hover {
    background-color: #3a00b0;
  }